body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

#cases-number {
  background-color: #4200ff;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 14px;
  border-top-right-radius: 14px;
}

#call-text {
  background-color: #4200ff;
}

.navbar-1-text {
  color: white;
  font-weight: 600;
}

.my-container {
  padding: 0 !important;
}

.container-4 {
  border-bottom: 2px solid blue;
}

.btn-edit {
  color: black !important;
  background-color: transparent !important;
  border-color: transparent !important;
}

.btn-edit:hover {
  color: black !important;
  background-color: transparent !important;
  border-color: transparent !important;
}

.btn-edit:active {
  color: black !important;
  background-color: transparent !important;
  border-color: transparent !important;
}

.image-col-1 {
  background-image: url("https://img.techpowerup.org/200828/background.png");
}

.carousel-text-box {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  margin-left: 5%;
  margin-top: 55px;
  /* width: 45%; */
}

#carousel-text {
  font-weight: 700;
  font-size: 2rem;
  margin-bottom: 25px;
}

#carousel-text-2 {
  font-weight: 600;
  font-size: 1.5rem;
  margin-bottom: 25px;
}

#carousel-text-3 {
  font-size: 1.3rem;
  font-weight: 500;
  margin-bottom: 25px;
}

#call-button {
  height: 50px;
  width: 250px;
  border: none;
  background-color: #ffc1c1;
  border-radius: 25px;
  font-size: 1.2rem;
  font-weight: 600;
}

.carousel-holder {
  background-image: url("https://img.techpowerup.org/200828/background.png");
  background-repeat: no-repeat;
  background-size: 100%, 300px;
  height: 420px;
}

#second-figure {
  position: absolute;
  height: 330px;
  width: 370px;
  background-color: #d7daf5;
  transform: rotate(-10.81deg);
  border-radius: 58px;
  margin-top: 35px;
}

#video-tag {
  width: 100%;
  height: 400px;
}

#video-heading-text {
  display: inline-block;
  font-size: 1.7rem;
  font-weight: 600;
  border-bottom: 3px solid blue;
  margin-bottom: 43px;
  padding-left: 10px;
}

#video-heading {
  border-left: 3px solid pink;
}

.spec-row {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#spec-para {
  width: 85%;
  font-size: 1.3rem;
  margin-top: 20px;
  text-align: center;
}

#spec-heading {
  font-size: 1.7rem;
  font-weight: 700;
}

#know-more-btn {
  margin-top: 25px;
  border: 0;
  width: 190px;
  height: 60px;
  background-color: pink;
  border-radius: 30px;
  font-size: 1.2rem;
}

.bottomblocks {
  margin-top: 20px;
}

#lastquote {
  text-align: center;
  color: white;
  display: inline-block;
}

.lastrow {
  background-color: #494848;
  margin-top: 40px;
  color: white;
  min-height: 200px;
  padding-top: 36px;
}

.mylastlist {
  list-style: none;
}

.blockrow {
  margin-top: 20px;
}

#knowimagecol {
  padding: 0 !important;
}

.buttonsrow {
  margin-top: 40px;
}

.vertical-button {
  margin-top: 20px;
  width: 300px;
  background: #807cff;
  height: 52px;
  border: none;
  font-weight: 600;
  border-radius: 20px;
  font-size: 20px;
}

#button-group {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.hoverimage {
  height: 420px;
  width: 80%;
}

.hoverimagecaption {
  text-align: center;
  font-size: 18px;
  font-weight: 600;
}

.blogcontainer {
  margin-top: 40px;
}

.blogsrow {
  margin-top: 30px;
}

/* ==================================== level 1 screening styles ============================================== */

.upper-row {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.questionslist-heading {
  margin-top: 20px;
}

.only-line {
  border-bottom: 2px solid #807CFF;
  width: 100%;
  margin: 16px 0px;
}

.question-box {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  line-height: 2;
}

.button-group {
  display: flex;
  justify-content: space-between;
  width: 220px;
}

.answer-button{
  border: 2px solid #807CFF;
  width: 100px;
  background: transparent;
  border-radius: 14px;
  height: 32px;
  line-height: 1;
}

.answer-no-button {
  margin-left: 24px;
}


.question-list-item {
  border: none !important;
  border-bottom: 2px solid #807CFF !important;
  background: transparent !important;
}

.total-row {
  display: flex;
  justify-content: space-between;
  padding: 10px;
}

.dark-text {
  font-size: 20px;
  padding: 5px;
}

.no-design-btn{
  border: none;
  background: transparent;
}

.next-button {
  border: none;
  background: #807CFF;
  width: 118px;
  border-radius: 20px;
  height: 40px;
  margin-left: 20px;
}

.screening-level-1 {
  background-color: #DFDEFF;
}